<template>
	<div>
		<div class="bsss">
			<div class="page__bd page__bd_spacing">
				<swiper :indicator-dots="indicatorDots" :autoplay="autoplay" :interval="interval" :duration="duration" :circular="circular" @change="swiperChange" @animationfinish="animationfinish">
					<div v-for="item in imgUrls" :key="index">
						<swiper-item>
							<image :src="item" class="slide-image" />
						</swiper-item>
					</div>
				</swiper>
			</div>
		</div>

		<div class="box-list">
			<div class="box-introduce">
				<div class="enen">{{name}}</div>
				<div class="box-key-list">
					作者：<span>{{msg.author}}</span>
				</div>
				<div class="box-key-list">出版社：<span>{{msg.press}}</span></div>
				<div class="box-key-list">状态：
					<icon v-if='msg.status == 1' type="success" size="20"></icon>
					<icon v-if='msg.status == 0' type="warn" size="20"></icon>
				</div>
				<div class="ssss">内容简介：{{msg.info}}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	var Fly = require("flyio/dist/npm/wx")
	var fly = new Fly
	export default {
		components: {

		},

		data() {
			return {
				name: '1111',
				//轮播图参数
				indicatorDots: true,
				autoplay: true,
				interval: 4000,
				duration: 900,
				circular: true,
				logs: [],
				imgUrls: [],
				msg: [],
				bookmsg: [],
				id: '',
			}
		},
		methods: {

			swiperChange(e) {},
			animationfinish(e) {},
		},
		onLoad(options) {
			this.id = options.id;
			fly.get('http://39.106.221.38:8080/clw-ser/findBookById?bookid=' + this.id)
				.then((response) => {
					this.name = response.data.book.name;
					this.imgUrls = response.data.pic;
					this.msg = response.data.book;
				})
		},

	}
</script>

<style>
	.bsss {
		border-bottom-style: outset;
		margin-left: 75rpx;
		height: 360rpx;
		width: 600rpx;
	}
	
	.box-list {
		box-orient: horizontal;
		width: 710rpx;
	}
	
	.box-introduce {
		height: 400rpx;
		display: box;
		box-orient: horizontal;
	}
	
	.box-key-list {
		margin-top: 40rpx;
		margin-left: 40rpx;
		margin-bottom: 20rpx;
		-webkit-box-flex: 1.0;
		font-size: 40rpx;
		border-bottom-style: outset;
	}
	
	.enen {
		font-weight: bold;
		margin-left: 40rpx;
		margin-top: 10rpx;
		margin-bottom: 20rpx;
	}
	
	.img-test {
		margin-left: 8rpx;
		margin-top: 20rpx;
		width: 200rpx;
		height: 200rpx;
	}
	
	.slide-image {
		width: 100%;
		height: 100%;
	}
	
	.ssss {
		margin-top: 40rpx;
		margin-left: 40rpx;
		margin-bottom: 20rpx;
		-webkit-box-flex: 1.0;
		font-size: 30rpx;
	}
	
	.wxapp-vue {
		height: 800rpx;
	}
</style>


